<template>
    <div class="shop">
        <div class="yinying" v-if="isShow3"></div>
        <nav v-if="!isShow5">
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left i1"></i>
            </a>
        </nav>
        <section class="shop_title">
                <div class="title_div1">
                    <img :src="'https://elm.cangdu.org/img/'+shop.image_path" class="title_img">
                    <div>
                        <h4>{{shop.name}}</h4>
                        <p>商家配送 / 分钟送达 / 配送费￥{{shop.float_delivery_fee}}</p>
                        <p>公告：{{shop.promotion_info}}</p>
                    </div>
                    <i class="el-icon-arrow-right"></i>
                </div>

        </section>
        <section class="shop_food">
            <div v-if="isShow2" class="food_s1"><span>商品</span></div>
            <div @click="isShow2=true" v-if="!isShow2"><span>商品</span></div>
            <div @click="isShow2=false" v-if="isShow2"><span>评价</span></div>
            <div v-if="!isShow2" class="food_s1"><span>评价</span></div>
        </section>
        <section class="commodity" v-if="isShow2">
            <div class="left">
                <ul>
                    <li v-for="(n,index) in category_list" :class="{listyle:changeLeftstyle == index}" @click="listyle(index)">{{n.name}}
                        <span v-if="getNum(n)>0" class="Number">{{getNum(n)}}</span>
                    </li>
                </ul>
            </div>
            <div class="right">
                <ul class="right_ul">
                    <li v-for="(m,i) in category_list2" class="right_ul_li">
                        <div><p class="ellipsis"><span>{{m.name}}</span><span>{{m.description}}</span></p><span>...</span></div>
                        <ul>
                            <li v-for="(a,j) in m.foods">
                                <div @click="toFoodDetail(a)">
                                    <div class="news" v-if="a.attributes[0]">
                                        <p>新品</p>
                                    </div>

                                    <img :src="'https://elm.cangdu.org/img/'+a.image_path" class="right_img">
                                    <h3>{{a.name}}<span v-if="a.attributes[0]">招牌</span></h3>
                                    <p class="food_p1">{{a.description ? a.description : ""}}</p>
                                    <p class="food_p2"><span>月售{{a.month_sales}}份</span><span>好评率{{a.satisfy_rate}}%</span></p>
                                    <p class="food_p3" v-if="a.attributes[0]">{{a.name}}</p>
                                </div>
                                <div class="food_content" >
                                    <div class="z1">
                                        <span>￥</span><span>{{a.specfoods[0].price}}</span>
                                    </div>
                                    <div class="z2" v-if="a.specifications[0]">
                                        <i class="el-icon-remove-outline z31" v-if="getCount(a)>0" style="color: #999"></i><span class="z22" v-if="getCount(a)>0">{{getCount(a)}}</span>
                                        <span @click="guige(i,j)" class="z21">选规格</span>
                                        <div class="Specifications" v-if="isShow4 == i+''+j">
                                            <div class="SS1">
                                                <h4>{{a.name}}</h4>
                                                <i class="el-icon-close" @click="isShow4=-1;isShow3=false" ></i>
                                            </div>
                                            <div class="SS2">
                                                <h5>{{a.specifications[0].name}}</h5>
                                                <ul>
                                                    <li v-for="(v,x) in a.specifications[0].values" :class="{liS:changeLeft == x}" @click="changeLiX(x)" class="ellipsis">
                                                        {{v}}
                                                    </li>
                                                </ul>
                                            </div>
                                            <div class="SS3">
                                                <div><span>￥</span><span>{{a.specfoods[changeLeft].price}}</span></div>
                                                <div @click="addShops(a.specfoods[changeLeft])">加入购物车</div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="z3" v-if="!a.specifications[0]">
                                        <i class="el-icon-remove-outline z31" @click="delShops(a.specfoods[0])" v-if="getCount(a.specfoods[0])>0"></i><span v-if="getCount(a.specfoods[0])>0">{{getCount(a.specfoods[0])}}</span>
                                        <i class="el-icon-circle-plus z32" @click="addShops(a.specfoods[0])"></i>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
            <div class="bottomC" v-if="isShow6">
                <div class="botA"  @click="isShow6 = !isShow6"></div>
                <div class="botB">
                    <div class="botB1"><h4>购物车</h4><div><span @click="clearShops"><i class="el-icon-delete"></i>清空</span></div></div>
                    <ul>
                        <li v-for="s in this.shops">
                            <div class="botB_li1"><p>{{s.name}}</p><p>{{s.specs != "" ? s.specs:""}}</p></div>
                            <div class="botB_li2"><span>￥</span><span>{{s.price}}</span></div>
                            <div class="botB_li3"><i class="el-icon-remove-outline" @click="delShops(s)"></i><span>{{s.quantity}}</span><i class="el-icon-circle-plus" @click="addShops(s)"></i> </div>
                        </li>
                    </ul>
                </div>
            </div>

            <div class="bottomZ" v-if="!isShowX"></div>
            <div class="bottom" v-if="!isShow5">
                <div @click="isShow6 = !isShow6">
                    <div :style="{'background':isShowX ? '#3190e8' : '#333'}" class="bo1"><i class="el-icon-shopping-cart-2"></i><span class="BS" v-if="isShowX">{{counts}}</span></div>
                    <div class="bo2">
                        <div>￥{{money}}.00</div>
                        <div>配送费￥{{shop.float_delivery_fee}}</div>
                    </div>
                </div>
                <div :style="{'background':isShowY ? '#4cd964' : '#333'}" class="bo3">
                    <span v-if="!isShowY">还差￥{{cost}}起送</span>
                    <span v-if="isShowY" style="color: white;font-size: .7rem;font-weight: 700;" @click="confirmOrder">去结算</span>
                </div>
            </div>
        </section>
        <section class="evaluate" v-if="!isShow2">
            <div class="e1">
                <div>
                    <p>{{shop.rating}}</p>
                    <p>综合评价</p>
                    <p>高于周边商家76.9%</p>
                </div>
                <div>
                    <p><span>服务态度</span><el-rate
                            v-model="4.7"
                            disabled
                            show-score
                            text-color="#ff9900"
                            score-template="{value}" class="e1R">
                    </el-rate></p>
                    <p><span>菜品评价</span><el-rate
                            v-model="4.8"
                            disabled
                            show-score
                            text-color="#ff9900"
                            score-template="{value}" class="e1R">
                    </el-rate></p>
                    <p><span>送达时间</span><span>分钟</span></p>
                </div>

            </div>
            <ul class="e2">
                <li v-for="(r,index) in ratings" :class="{e2_li_style:e2_li_i == index}" @click="e2style(index)">
                    {{r.name}}({{r.count}})
                </li>
            </ul>
            <ul class="e3">
                <li v-for="u in ratingu">
                    <img src="https://elm.cangdu.org/img/default.jpg">
                    <div>
                        <div class="h">
                            <div>
                                <p>{{u.username}}</p>
                                <p><el-rate
                                        v-model="u.rating_star"
                                        disabled
                                        text-color="#ff9900"
                                        class="e1R"></el-rate><span>{{u.time_spent_desc}}</span></p>
                            </div>
                            <p>{{u.rated_at}}</p>
                        </div>
                        <ul >
                            <li v-for="r in u.item_ratings">
                                <img :src="'https://fuss10.elemecdn.com/'+r.image_hash" class="ratingImg">
                            </li>
                        </ul>
                        <ul class="food_name">
                            <li v-for="r in u.item_ratings" class="ellipsis">
                                {{r.food_name}}
                            </li>
                        </ul>
                    </div>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Shop",
        data(){
            return{
                shop:[],
                isShow:false,
                isShow2:true,
                category_list:[],
                category_list2:[],
                changeLeftstyle:0,
                ratings:[],
                e2_li_i:0,
                ratingu:[],
                shop1:[],
                shop2:[],
                isShow3:false,
                isShow4:-1,
                changeLeft:0,
                isShow5:false,
                isShow6:false,
                shops:[],
                money:"",
                counts:"",
                isShowX:false,
                isShowY:false,
                cost:"",
                geohash:"",
                entities:[],
                isShow7:false,
            }
        },
        created() {
            this.shop = JSON.parse(localStorage.getItem("shop"));
            this.geohash = JSON.parse(localStorage.getItem("position"))[0].geohash;
            this.cost = this.shop.float_minimum_order_amount;
            if (this.shop.activities[0]) {
                this.isShow = true;
            }else{
                this.isShow = false;
            }

            this.getShops;

            //获取店铺食品种类
            this.axios.get("/shopping/getcategory/" + this.shop.id).then(res=>{
                // console.log(res);
                this.category_list = res.category_list;
                this.category_list2 = res.category_list;
                // console.log(res.category_list[0].foods[0].attributes[0].icon_name)
                // console.log(res.category_list[0].foods)
            }).catch(err=>{});
            //获取评价分类
            this.axios.get("https://elm.cangdu.org/ugc/v2/restaurants/"+this.shop.id+"/ratings/tags").then(res=>{
                // console.log(res);
                this.ratings = res;
            }).catch(err=>{})
            //获取评价信息
            this.axios.get("https://elm.cangdu.org/ugc/v2/restaurants/"+this.shop.id+"/ratings?offset=0&limit=10").then(res=>{
                // console.log(res);
                this.ratingu = res;
            }).catch(err=>{});
        },
        methods:{
            listyle(i){
                this.changeLeftstyle = i;
                let selector = ".right>ul>li:nth-child(" + (i + 1) + ")";
                this.$el.querySelector(selector).scrollIntoView({
                    behavior: "smooth",  // 平滑过渡
                    block: "start",  // 上边框与视窗顶部平齐。默认值
                });

            },
            e2style(i){
                this.e2_li_i = i;
            },
            guige(i,j){
                this.isShow4 = i+""+j;
                this.isShow3 = true;
            },
            changeLiX(x){
                this.changeLeft = x;
            },
            addShops(a){
                // console.log(a);
                if(a.id){
                    a.food_id = a.id;
                }
                if(a.specs.length == 1){
                    a.specs = a.specs[0].value;
                }
                let shopS;
                if(localStorage.getItem('shops')==null){
                    shopS = [];
                    shopS.push({
                        restaurant_id:a.restaurant_id,
                        item_id:a.item_id,
                        id:a.food_id,
                        name:a.name,
                        packing_fee:a.packing_fee,
                        quantity:1,
                        sku_id:a.sku_id,
                        price:a.price,
                        specs:a.specs,
                        stock:a.stock
                    });
                    this.shops = shopS;
                    localStorage.setItem("shops",JSON.stringify(shopS));
                }else{
                    shopS = JSON.parse(localStorage.getItem('shops'));
                    let aaa = true;
                    for(let m = 0;m < shopS.length;m++){
                        if(shopS[m].id == a.food_id){
                            shopS[m].quantity += 1;
                            aaa = false;
                        }
                    }
                    if(aaa){
                        shopS.push({
                            restaurant_id:a.restaurant_id,
                            item_id:a.item_id,
                            id:a.food_id,
                            name:a.name,
                            packing_fee:a.packing_fee,
                            quantity:1,
                            sku_id:a.sku_id,
                            price:a.price,
                            specs:a.specs,
                            stock:a.stock});
                    }
                    this.shops = shopS;
                    localStorage.setItem("shops",JSON.stringify(shopS));
                }
                this.counts += 1;
                this.money += a.price;
                this.isShowzzz;
                this.isShow3 = false;
                this.isShow4 = -1;
                this.changeLeft = 0;
            },
            delShops(a){
                if(a.id){
                    a.food_id = a.id;
                }

                let shopS = JSON.parse(localStorage.getItem("shops"));
                for(let m = 0;m < this.shops.length;m++){
                    if(this.shops[m].id == a.food_id){
                        if(this.shops[m].quantity>1){
                            this.shops[m].quantity -= 1
                        }else{
                            this.shops.splice(m,1);
                        }
                        this.counts -= 1;
                        this.money -= a.price;
                    }
                }
                for(let m = 0;m < shopS.length;m++){
                    if(shopS[m].id == a.food_id){
                        if(shopS[m].quantity>1){
                            shopS[m].quantity -= 1
                        }else{
                            shopS.splice(m,1);
                        }
                    }
                }
                if(this.shops == ""){
                    this.isShow6 = false;
                }
                localStorage.setItem("shops",JSON.stringify(shopS));
                this.isShowzzz;
            },
            clearShops(){
                let shopS = JSON.parse(localStorage.getItem("shops"));
                if(localStorage.getItem("shops")){
                    for(let i = 0;i< shopS.length; i++){
                        if(this.shop.id == shopS[i].restaurant_id){
                            shopS.splice(i,1);
                            i--;
                        }
                    }
                }
                localStorage.setItem("shops",JSON.stringify(shopS));
                this.counts = 0;
                this.money = 0;
                this.isShow6 = false;
                this.shops = [];
                this.isShowzzz;
            },
            getCount(a){
                let num=0;
                for(let i = 0;i < this.shops.length;i++){
                    if(this.shops[i].name == a.name){
                        num += this.shops[i].quantity;
                    }
                }
                return num;
            },
            getNum(n){
                let num=0;
                for(let i = 0;i < this.shops.length;i++){
                    for (let j=0;j<n.foods.length;j++) {
                        if(this.shops[i].item_id == n.foods[j].item_id){
                            num += this.shops[i].quantity;
                        }
                    }

                }
                return num;
            },
            confirmOrder(){
                this.entities = [];
                let obj=[];
                for (let i=0;i<this.shops.length;i++){
                    obj.push({
                        attrs:[],
                        extra:{},
                        id:this.shops[i].id,
                        name:this.shops[i].name,
                        packing_fee:this.shops[i].packing_fee,
                        price:this.shops[i].price,
                        quantity:this.shops[i].quantity,
                        sku_id:this.shops[i].sku_id,
                        specs:this.shops[i].specs,
                        stock:this.shops[i].stock,
                    })
                }
                this.entities.push(obj);
                console.log(this.shop.id,this.geohash,this.entities);
                this.axios.post("/v1/carts/checkout",{
                    restaurant_id:this.shop.id,
                    geohash:this.geohash,
                    entities:this.entities,
                }).then(res=>{
                    console.log(res);
                    this.$router.push({path:"/confirmOrder",query:{cart_id:res.id}});
                }).catch(err=>{
                    console.log(err)
                });
            },
            toFoodDetail(f){
                this.$router.push("/foodDetail");
                localStorage.setItem("foodDetail",JSON.stringify(f));
            }
        },
        computed:{
            //处理购物车数据
            getShops(){
                this.shops = [];
                this.money = 0;
                this.counts = 0;
                let shopS = JSON.parse(localStorage.getItem("shops"));
                if(localStorage.getItem("shops")){
                    for(let i = 0;i< shopS.length; i++){
                        if(this.shop.id == shopS[i].restaurant_id){
                            this.shops.push(shopS[i]);
                            this.counts = this.counts + shopS[i].quantity;
                            this.money = this.money + shopS[i].quantity * shopS[i].price;
                        }
                    }
                }
                this.isShowzzz;
            },
            isShowzzz(){
                if(this.counts>0){
                    this.isShowX = true;
                }else{
                    this.isShowX = false;
                }
                if(this.money>=this.cost){
                    this.isShowY = true;
                }else{
                    this.isShowY = false;
                    this.cost = this.cost - this.money;
                }
                if(this.shops == []){
                    this.isShow6 = false;
                }
            },

        }
    }
</script>

<style scoped>
    .shop{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    .yinying{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background-color: rgba(0,0,0,0.4);
        z-index: 15;
    }
    a{
        text-decoration: none;
    }
    nav{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 2rem;
        z-index: 12;
    }
    nav i{
        font-size: 1rem;
        font-weight: 900;
        color:#fff
    }
    .shop_title{
        position: relative;
        z-index: 10;
        background-color: rgba(119,103,137,.43);
        padding: .4rem 0 .4rem .4rem;
        width: 98.5%;
        overflow: hidden;
    }
    .title_div1{
        width: 100%;
        overflow: hidden;
        position: relative;
    }
    .title_img{
        width: 2.9rem;
        height: 2.9rem;
        border-radius: .15rem;
        margin-right: .3rem;
        float: left;
    }
    .title_div1 div{
        flex: 1;
        float: left;
        margin-top: 0.15rem;
    }
    .title_div1 h4{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        width: 100%;
        margin-bottom: .4rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .title_div1 p{
        font-size: .5rem;
        color: #fff;
        margin-bottom: .4rem;
    }
    .title_div1 i{
        font-size: 0.9rem;
        color: #fff;
        position: absolute;
        top: 50%;
        right: 0.4rem;
        transform: translateY(-50%);
    }
    .shop_food{
        display: flex;
        background-color: #fff;
        padding: .3rem 0 .6rem;
        border-bottom: 1px solid #ebebeb;
    }
    .shop_food div{
        flex: 1;
        text-align: center;
        border-color: #3190e8;
        padding: .1rem .1rem;
        color: #666;
    }
    .shop_food div span{
        font-size: .65rem;
    }
    .food_s1 span{
        color: #3190e8;
        border-bottom: .12rem solid #3190e8;
    }
    .commodity{
        height: 19rem;
        display: flex;
        padding-bottom: 2rem;
    }
    .left{
        flex: 1.5;
        overflow-y: auto;
        overflow-x: hidden;
    }
    .left li{
        padding: .7rem .3rem;
        border-bottom: .025rem solid #ededed;
        box-sizing: border-box;
        position: relative;
        font-size: .6rem;
        color: #666;
        line-height: 1rem;
    }
    .listyle{
        border-left: .15rem solid #3190e8;
        background-color: #fff;
        font-weight: 700;
    }
    .Number{
        position: absolute;
        top: .1rem;
        right: .1rem;
        background-color: #ff461d;
        line-height: .6rem;
        text-align: center;
        border-radius: 50%;
        border: .025rem solid #ff461d;
        min-width: .6rem;
        height: .6rem;
        font-size: .5rem;
        color: #fff;
    }
    .right{
        flex:4;
        overflow-y: auto;
    }

    .right_ul_li {
        width: 93%;
    }
    .right_ul_li>div{
        width: 100%;
        padding: .4rem;
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .right_ul_li>div>p{
        white-space: nowrap;
    }
    .right_ul_li>div>p>span:nth-child(1){
        font-size: .7rem;
        color: #666;
        font-weight: 700;
    }
    .right_ul_li>div>p>span:nth-child(2){
        margin-left: 0.3rem;
        font-size: .5rem;
        color: #999;
        width: 30%;
    }
    .right_ul_li ul{
        width: 100%;
    }
    .right_ul_li li{
        width: 100%;
        background-color: #fff;
        padding: .6rem .4rem;
        border-bottom: 1px solid #f8f8f8;
        position: relative;
        overflow: hidden;
    }
    .news{
        color: rgb(94, 196, 82);
        border-color: rgb(94, 196, 82);
        position: absolute;
        top: 0;
        left: 0;
        background-color: #4cd964;
        width: 2rem;
        height: 2rem;
        display: flex;
        align-items: flex-end;
        transform: rotate(-45deg) translate(-.1rem,-1.5rem);
        border-radius: 0;
        margin-left: 0;
    }
    .news p{
        font-size: .4rem;
        color: #fff;
        text-align: center;
        flex: 1;
        transform: scale(.8) translate(.1rem,-.1rem);
    }
    .right_img{
        width: 2rem;
        height: 2rem;
        display: block;
        float: left;
        margin-right: 0.3rem;
        margin-bottom: 3rem;
    }
    .right_ul_li li h3{
        font-size: .7rem;
        display: flex;
        justify-content: space-between;
        margin-bottom: .2rem;
    }
    .right_ul_li li h3>span{
        display: inline-block;
        color: rgb(240, 115, 115);
        font-size: .3rem;
        height: .4rem;
        line-height: .4rem;
        padding: .1rem;
        border: 1px solid rgb(240, 115, 115);
        border-radius: .3rem;
        margin-right: .1rem;
        transform: scale(.8);
    }
    .right_ul_li li>p{
        line-height: .8rem;
    }
    .right_ul_li li>p>span{
        font-size: .5rem;
        color: #333;
        margin-right: 0.3rem;
    }
    .food_p1,.food_p2{
        font-size: .5rem;
        color: #999;
        line-height: .6rem;
        margin: 0.25rem;
    }
    .food_p3{
        color: rgb(241, 136, 79);
        font-size: .3rem;
        border: 1px solid rgb(240, 115, 115);
        border-radius: .3rem;
        padding:0 .08rem;
        display: inline-block;
        transform: scale(.8);
    }
    .food_content{
        margin-left: 2.3rem;
        font-size: 0;
